import React from 'react';

const axios = require('axios').default;

class MessageRequest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      text: "",
      uppercase: false,
      reverse: false,
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange (event) {
    const target = event.target;
    const value = "checkbox" === target.type ? target.checked : target.value;
    const name = target.name;
    this.setState({ [name]: value });
  }

  handleSubmit (event) {
    event.preventDefault();
    console.log("Sending Request " + JSON.stringify(this.state));

    axios.post('/api/send-request', this.state)
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });

  }

  render () {
    return (
      <div>
        <h2>Requests</h2>
        <form id="requests" method="post" onSubmit={this.handleSubmit}>
          <input id="request-text"
            type="text"
            name="text"
            autoFocus="autofocus"
            onChange={this.handleChange}
            value={this.state.text} />
          <div id="request-uppercase-option">
            <input id="request-uppercase"
              type="checkbox"
              name="uppercase"
              onChange={this.handleChange}
              checked={this.state.uppercase} />
            <label htmlFor="request-uppercase">Uppercase</label>
          </div>
          <div id="request-reverse-option">
            <input id="request-reverse"
              type="checkbox"
              name="reverse"
              onChange={this.handleChange}
              checked={this.state.reverse} />
            <label htmlFor="request-reverse">Reverse</label>
          </div>
          <button type="submit">Send Request</button>
        </form>
      </div>
    );
  }
}

export default MessageRequest;